.contact{
    padding: 40px 0;
    @media (min-width: 950px) {
      padding: 50px 0;
      padding-bottom: 70px;
    }
    & .contact-wrapper{
        @media (min-width: 1366px) {
            display: grid;
          grid-template-columns: 1fr 1fr;
          grid-gap: 5px;
      }
      @media (min-width: 1500px) {
          grid-gap: 10px;
      }
    }
    & .contact-mrg{
      border-bottom: 1px solid #C2C9CC;
      margin-bottom: 20px;
      @media (min-width: 1366px) {
        margin-right: 35px;
        margin-bottom: 0;
      }
      @media (min-width: 1500px) {
        margin-right: 70px;
      }
    }
    & .contact-brd{
      border-bottom: 1px solid @color-grey;
    }
    & .contact-pdl{
        padding-top: 17px;
        @media (min-width: 768px) {
            padding-left: 30px;
            margin-top: 20px;
        }
    }
    & .contact-brd-r{
      border-bottom: 1px solid @color-grey;
      padding-top: 17px

    }
}
.contact .contact-brd-r{
  @media (min-width: 768px) {
    border-bottom: none;
    margin-top: 20px;
  }
}
.contact .contact-brd-r{
  @media (min-width: 1200px) {
    border-right: 1px solid @color-grey;
  }
}

.contact .contact-mainbox--grey{
    background-color: #f9f9f9;
    @media (min-width: 767px) and (max-width: 1365px) {
        padding-bottom: 20px;
    }
}


.form-title{
    font-size: 22px;
    line-height: 30px;
    font-family: 'Oswald', sans-serif;
    font-weight: 400;
    color: @color-blue2;
    margin-bottom: 10px;
    @media (min-width: 1100px) {
        font-size: 40px;
        line-height: 52px;
    }
}
.form-text{
    font-size: 15px;
    line-height: 20px;
    font-weight: 500;
    @media (min-width: 1100px) {
        font-size: 17px;
        line-height: 24px;
    }
}
/* checkbox */
.custom-checkbox {
    position: absolute;
    z-index: 0;
    opacity: 0;
}
.custom-checkbox+label {
    /* display: inline-flex; */
    align-items: baseline;
    user-select: none;
    cursor: pointer;
}
.custom-checkbox+label::before {
    content: '';
    display: inline-block;
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 1px solid @color-grey;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%;
    position: absolute;
    top: 2px;
    left: -30px;
    /* @media (max-width: 407px) {
        top: -6px;
      } */
}
.custom-checkbox:checked+label::before {
    border-color: #0b76ef;
    background-color: #09388A;
    background-image: url(../img/check.svg);
}
/* стили при наведении курсора на checkbox */
.custom-checkbox:not(:disabled):not(:checked)+label:hover::before {
    border-color: @color-blue-grey;
}


/* / checkbox */

.contact-form{
    background-color: @color-white2;
    @media (min-width: 1500px) {
      padding-left: 30px;
    }
    & .form-box{
        padding: 20px;
        @media (min-width: 768px) {
            display: flex;
            flex-wrap: wrap;
            padding: 20px 20px 0;
        }
    }
    & .form-elem{
        position: relative;
        display: flex;
        flex-direction: column;
        height: 60px;
        justify-content: flex-end;
        @media (min-width: 768px) {
            width: 48%;
            &:nth-child(1){
                margin-right: 20px;
            }
            &:nth-child(3){
                margin-right: 20px;
            }
        }
        &--long{
            @media (min-width: 768px) {
                width: 100%;
            }
        }
        &--checkbox{
            flex-direction: row;
            border-bottom: none;
            justify-content: flex-start;
            margin-top: 20px;
            height: 40px;
            @media (min-width: 768px) {
                width: 100%;
            }
        }
        /* &--modal{
            margin-top: 40px;
            border-top: 1px solid #c2c9cc;
        } */
        & label, .form-elem__label{
            position: absolute;
            font-size: 14px;
            line-height: 26px;
            font-weight: 500;
            color: #001e42;
            margin-bottom: 5px;
            pointer-events: none;
        }

        &.form-elem__label.form-elem-text{
            top: 5px;
        }
        & input{
            display: block;
            border: none;
            background-color: @color-white2;
            font-weight: 500;
            color: @color-black;
            height: 25px;
            font-family: 'Roboto', sans-serif;
            font-size: 16px;
            line-height: 24px;
            font-weight: 500;
            outline: 0;
            border-bottom: 1px solid @color-grey;
            resize: none;
            /* appearance: none;
            -moz-appearance: none;
            -webkit-appearance: none; */
            box-shadow: none !important;
        }
        & .form-elem__input{
            display: block;
            border: none;
            background-color: @color-white2;
            font-weight: 500;
            color: @color-black;
            height: 50px;
            font-family: 'Roboto', sans-serif;
            font-size: 16px;
            line-height: 24px;
            font-weight: 500;
            outline: 0;
            border-bottom: 1px solid @color-grey;
            border-top: 1px solid @color-grey;
            resize: none;
            appearance: none;
            -moz-appearance: none;
            -webkit-appearance: none;
            box-shadow: none !important;
        }
    }
    & .genetal-order{
        background-color: @color-blue1;
        width: 100%;
        padding: 15px 5px;
        border: none;
        color: @color-white;
        font-size: 17px;
        line-height: 26px;
        cursor: pointer;
        @media (min-width: 768px) {
            width: 216px;
            margin-left: 20px;
          }
        &:hover{
            background-color: #0C7CFF;
        }
        @media (min-width: 950px) {
          width: 216px;
        }
        &--modal{
            width: 100%;
            margin: 0;
            @media (min-width: 991px) {
                width: 216px;
              }
        }
    }
    & .form-error{
        display: none;
        color: @color-red;
        font-weight: 500;
        font-size: 14px;
        line-height: 24px;
        letter-spacing: -0.28px;
        position: absolute;
        bottom: -25px;
    }
}
.form-elem.form-elem--checkbox label{
    color: #52667F;
    left: 30px;
    font-weight: 500;
    @media (max-width: 407px) {
        top: 5px;
        line-height: 20px;
      }
}
.contact-form .form-elem.form-elem--long .form-elem__label{
    top: 30px;
}
.form-elem__input:focus::placeholder{
    opacity: 1;
}
.form-elem__input::placeholder{
    color: #001E42;
    opacity: 0;
}
/* .contact-form .form-elem--modal:hover{
    border-top: 1px solid @color-blue-grey;
} */
//ошибка в форме
.form-elem.error{
    border-bottom: 1px solid @color-red;
}
// если инпут активен + .active
.form-elem.active label{
    top: 5px;
    color: #52667F;
    font-size: 13px;
    font-weight: 400;
}
.contact-form .form-elem.active .form-elem__label{
    top: 5px;
    color: #52667F;
    font-size: 13px;
    font-weight: 400;
}

.contact-form .form-elem--long{
    height: 80px;
    margin-top: 15px;
}

.flag::after{
    content: " ";
    position: absolute;
    display: block;
    width: 20px;
    height: 14px;
    background-image: url(../img/flag.svg);
    background-repeat: no-repeat;
    background-size: auto;
    right: 0;
    top: 50%;

}
.form-elem input:focus .form-elem label{
    visibility: visible;
}
.form-head{
    position: relative;
    background-color: @color-white;
    padding: 20px;
    margin-bottom: 2px;
    overflow: hidden;
    z-index: -0;
    border-bottom: 2px solid #F3F3F3;
    @media (min-width: 900px) {
        height: 180px;
    }
    @media (min-width: 1200px) {
        padding: 50px;
    }
    &--grey{
        background-color: @color-white2;
    }
    &::before{
        content: "";
        min-width: 150px;
        min-height: 150px;
        width: inherit;
        top: -80px;
        right: 10px;
        position: absolute;
        background-image: url(../img/figure3.svg);
        background-repeat: no-repeat;
        background-size: auto;
        z-index: -1;
        @media (min-width: 1200px) {
            min-width: 300px;
            min-height: 300px;
            top: -170px;
            right: 58px;
            background-size: contain;
        }
    }
    &::after{
        content: "";
        display: block;
        height: 2px;
        width: 70px;
        background-color: @color-blue1;
        position: absolute;
        bottom: 0;
    }
}
.contact-flex{
    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 20px;
    }
}
.form-elem a{
    color: #52667F;
    text-decoration: underline;
    font-size: 14px;
    line-height: 20px;
    pointer-events: all;
    margin-left: 5px;
    display: inline-block;
    display: -webkit-inline-block;
}
.form-elem a:hover{
    color: #035EC8;
}
.form-elem:hover input{
    border-bottom: 2px solid #c2c9cc;
}
.form-elem:hover .form-elem__input{
    border-top: 2px solid #c2c9cc;
    border-bottom: 2px solid #c2c9cc;
}
