.preview__video{
    position: relative;
    min-height: 190px;
    /* background: url(../img/preview-video.jpg) center / cover no-repeat; */
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    z-index: 1;
    overflow: hidden;
    &  a {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
    }
    & img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform .2s;
    }
}
.preview__video:hover img {
    transform: scale(1.1);
}
.preview__video:hover .preview__video-text::after {
    background: @color-blue1;
    transform: skewY(14deg) translateY(22%);
}
.preview__box{
    position: absolute;
    height: 140px;
    opacity: 90%;
    bottom: 0;
    @media (min-width: 768px) {
        height: 150px;
    }
    @media (min-width: 1200px) {
        height: 250px;
    }
}
.preview__video-text{
    height: 100%;
    color: @color-white;
    font-size: 17px;
    line-height: 22px;
    font-weight: 500;
    padding: 20px 20px 10px;
    max-width: 200px;
    cursor: pointer;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @media (min-width: 768px) {
        max-width: 280px;
        padding: 20px 10px;
    }
    @media (min-width: 1200px) {
        padding: 40px;
    }
    &::before{
        content: "";
        display: block;
        width: 50px;
        height: 35px;
        position: relative;
        z-index: 1;
        background: url(../img/preview-play.svg) center no-repeat;
        background-size: contain;
        margin-bottom: 10px;
        @media (min-width: 850px) {
          width: 75px;
          height: 52px;
      }
    }

    @media (min-width: 1100px) {
        font-size: 22px;
        line-height: 30px;
        max-width: 400px;
    }
}
.preview__video .video-file{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -o-object-fit: cover;
    object-fit: cover;
}