.studies__download::before{
    content: "";
    display: block;
    background: url(../img/studies-doc.svg) center / cover no-repeat;
    width: 21px;
    height: 30px;
    margin-right: 12px;
}
.studies__download{
    font-size: 16px;
    line-height: 20px;
    color: @color-blue1;
    font-weight: 500;
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    width: max-content;
    @media (min-width: 850px) {
      font-size: 17px;
      line-height: 26px;
  }
}
.studies__download:hover .studies__span{
    text-decoration: underline;
}
.studies__example{
  margin-bottom: 35px;
    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 75px;
    }
}
.studies__box{
    & .general-title{
        padding-bottom: 15px;
    }
    @media (min-width: 768px) {
        order: 0;
        flex-grow: 1;
    }
}
.studies-btn{
    @media (min-width: 768px) {
        order: 0;
    }
}
