body{
    position: relative;
}
.modal-shadow{
    overflow-y: scroll;
    position: fixed;
    top: 0;
    z-index: 100;
    width: 100%;
    height: 100%;
    background-color: #001E4250;/* 50 - прозрачность */
    display: none;
    justify-content: flex-end;
    &.active {
      display: flex;
    }  
}
.modal-shadow .custom-checkbox{
    z-index: 0;
}
.modal-shadow .form-head{
    margin-bottom: 20px;
    padding: 0 50px;
    border-bottom: 2px solid #F3F3F3;
    @media (max-width: 991px) {
      margin-bottom: 0;
      padding: 20px;

    }
}
.call-deck .form-elem{
    width: 100%;
}
.order{
    position: relative;
    width: 45%;
    height: 100%;
    background-color: @color-white2;
    display: flex;
    justify-content: center;
    padding-top: 20vh;
    & .contact-form{
      @media (min-width: 992px) {
        padding: 0 50px;
      } 
    }
    &.call-deck{
        width: 450px;
        padding-top: 23vh;
    }
    & .form-head{
        height: 120px;
    }
    &--mobile{
      @media (max-width: 991px) {
        background-color: rgba(0, 0, 0, 0);
        justify-content: center;
        width: 100%;
        margin: 0 auto;
        padding-bottom: 25vh;
        & .order__box{
            padding: 0;
        }
      }   
    }
}
.call-deck.order--mobile{
  @media (max-width: 991px) {
    padding-bottom: 43vh;
  }
}
.order__box{
  @media (min-width: 992px) {
    flex-grow: 1;
  } 
  
  @media (max-width: 991px) {
    &--mobile{
      position: relative;
      width: 296px;
      background-color: #f9f9f9;
    }
 } 
}
.modal-form-box{
  padding: 20px 0;
  @media (min-width: 1610px) {
    display: flex;
    flex-wrap: wrap;
  }
  @media (max-width: 991px) {
    padding: 0 20px 20px;
  }
  & .form-elem{
    width: 100%;
/*     height: 50px; */
    @media (min-width: 1610px) {
      width: 48%;
    }
    &--long{
      @media (min-width: 1610px) {
        width: 100%;
      }
    }
  }
}
.modal-form-box .form-elem--checkbox{
  width: 100%;
}
.modal-form-box.modal-form-call{
  & .form-elem{
    width: 100%;
    margin-right: 0;
  }
}
.modal-form-box .form-elem:hover input{
  border-bottom: 2px solid #c2c9cc;
}
/* .modal-form-box .form-elem:hover .form-elem__input{
  border-bottom: 1px solid @color-blue-grey;
} */
.order .form-head::before{
  @media (min-width: 992px) {
    display: none;
  }
}
.order::before{
  content: "";
  min-width: 250px;
  min-height: 250px;
  top: -150px;
  right: calc(50% - 120px);
  position: absolute;
  background-image: url(../img/figure3.svg);
  background-repeat: no-repeat;
  background-size: contain;
  @media (max-width: 991px) {
    display: none;
  }
}
