/* Roboto*/
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('../fonts/roboto/Roboto-Regular.woff2') format('woff2'), url('../fonts/roboto/Roboto-Regular.woff') format('woff');
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url('../fonts/roboto/Roboto-Medium.woff2') format('woff2'), url('../fonts/roboto/Roboto-Medium.woff') format('woff');
}
/*  */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url('../fonts/oswald/Oswald-Regular.woff2') format('woff2'), url('../fonts/oswald/Oswald-Regular.woff') format('woff');
}
:root {
  box-sizing: border-box;
}
*,
::before,
::after {
  box-sizing: inherit;
  -webkit-tap-highlight-color: transparent;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
  color: #001E42;
  text-transform: none;
  background-color: #F3F3F3;
  height: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
}
@media (min-width: 768px) {
  .container {
    max-width: 714px;
    padding-left: 3px;
    padding-right: 3px;
  }
}
@media (min-width: 900px) {
  .container {
    max-width: 1760px;
    padding-left: 20px;
    padding-right: 20px;
  }
}
.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
}
@media (max-width: 1199px) {
  .header {
    background-color: #001E42;
  }
}
@media (min-width: 1200px) {
  .header.scrolled-deck {
    background-color: #FFFFFF;
  }
  .header.scrolled-deck .navigation__list .navigation__item a {
    color: #001E42;
  }
  .header.scrolled-deck .navigation__box a {
    color: #09388A;
  }
  .header.scrolled-deck .mail-icon::after {
    opacity: 1;
  }
  .header.scrolled-deck .phone-icon::after {
    opacity: 1;
  }
}
.header.scrolled-deck .navigation__box a:hover {
  color: #035EC8;
}
.main-nav {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  /* position: relative; */
}
@media (max-width: 991px) {
  .main-nav {
    margin-bottom: 0;
    /*     margin: 10px 0; */
    min-height: 50px;
  }
}
@media (min-width: 992px) {
  .main-nav {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.main-nav-left,
.main-nav-right {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 1200px) {
  .main-nav-left,
  .main-nav-right {
    width: 50%;
  }
}
@media (min-width: 1520px) {
  .main-nav-left,
  .main-nav-right {
    width: 45%;
  }
}
@media (max-width: 1199px) {
  .main-nav-left {
    flex: 1 0 auto;
  }
}
@media (min-width: 1200px) {
  .main-nav-left {
    padding-right: 25px;
  }
}
.main-nav-right {
  flex-wrap: wrap;
  justify-content: space-between;
}
@media (max-width: 991px) {
  .main-nav-right {
    width: 75%;
  }
}
@media (min-width: 1200px) and (max-width: 1519px) {
  .main-nav-right {
    padding-left: 25px;
  }
}
.nav-logo {
  background: #FFFFFF;
  display: block;
  width: 90px;
  height: 90px;
  flex: 0 0 auto;
}
@media (max-width: 1199px) {
