    top: 5px;
    background-color: transparent;
  }
} */
@media (max-width: 1199px) {
  .nav-logo.open {
    width: 40px;
    height: 40px;
    top: 5px;
    background-color: transparent;
  }
}
.nav-logo.scrolled {
  width: 60px;
  height: 60px;
}
@media (max-width: 1199px) {
  .nav-logo.scrolled {
    width: 40px;
    height: 40px;
    top: 5px;
