  }
}
.navigation {
  flex-grow: 1;
}
@media (max-width: 991px) {
  .navigation {
    display: none;
    position: absolute;
    width: 100%;
    left: 0;
    top: 50px;
    border-top: 1px solid #035EC8;
    background-color: #001E42;
    padding-bottom: 30px;
  }
  .navigation.open {
    display: block;
  }
}
.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  /*   @media (min-width: 1520px) {
    justify-content: flex-end;
  } */
}
@media (max-width: 991px) {
  .navigation__list {
    flex-direction: column;
    align-items: center;
  }
}
.navigation__list .navigation__item {
  position: relative;
  margin-left: 20px;
}
@media (max-width: 991px) {
  .navigation__list .navigation__item {
    margin-top: 25px;
    margin-left: 0;
  }
}
@media (min-width: 1200px) {
  .navigation__list .navigation__item {
    margin-left: 15px;
  }
}
@media (min-width: 1350px) {
  .navigation__list .navigation__item {
    margin-left: 30px;
  }
}
@media (min-width: 1670px) {
  .navigation__list .navigation__item {
    margin-left: 45px;
  }
}
.navigation__list .navigation__item a {
  font-size: 17px;
  line-height: 24px;
  font-weight: 500;
  color: #001E42;
}
@media (max-width: 1199px) {
  .navigation__list .navigation__item a {
    font-weight: 500;
    color: #FFFFFF;
  }
}
.navigation__list .navigation__item:hover a {
  color: #035EC8;
}
.navigation__item a::after {
  content: " ";
  width: 100%;
  height: 2px;
  background-color: #035EC8;
  position: absolute;
  left: 0;
  bottom: -3px;
  transform: scaleX(0);
}
.navigation__item:hover a::after {
  transform: scaleX(1);
  transition: transform 0.25s;
}
@media (min-width: 768px) {
  .navigation__item--btn {
    display: none;
  }
}
.mail-icon::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  /*  margin-right: 20px; */
  background: url(../img/nav-e-mail.svg) center center no-repeat;
  background-size: auto;
}
@media (min-width: 1200px) {
  .mail-icon::before {
    margin-right: 15px;
  }
}
.mail-icon::after {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
  width: 30px;
  height: 30px;
  /*   margin-right: 10px; */
  background: url(../img/nav-e-mail-blue.svg) center center no-repeat;
  background-size: auto;
  opacity: 0;
}
@media (max-width: 1200px) {
  .mail-icon::after {
    display: none;
  }
}
.mail-icon.active::before,
.mail-icon.active::after {
  display: none;
}
.phone-icon::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  /*   margin-right: 20px; */
  background: url(../img/nav-phone.svg) center center no-repeat;
  background-size: auto;
}
@media (min-width: 1200px) {
  .phone-icon::before {
    margin-right: 15px;
  }
}
.phone-icon::after {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
  width: 30px;
  height: 30px;
  /*   margin-right: 20px; */
  background: url(../img/nav-phone-blue.svg) center center no-repeat;
  background-size: auto;
  opacity: 0;
}
@media (max-width: 1200px) {
  .phone-icon::after {
    display: none;
  }
}
.phone-icon.active::before,
.phone-icon.active::after {
  display: none;
}
.navigation__cont-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
}
@media (min-width: 1200px) {
  .navigation__cont-wrap {
    position: relative;
  }
}
@media (max-width: 1199px) {
  .navigation__cont-wrap {
    height: 40px;
    width: 60px;
    padding-right: 5px;
    margin-left: 5px;
  }
}
@media (min-width: 1200px) {
  .navigation__cont-wrap {
    margin-right: 5px;
    padding: 0;
    justify-content: space-between;
  }
}
@media (max-width: 1199px) {
  .navigation__cont-wrap {
    border-right: 1px solid #09388A;
    margin-right: 0;
  }
}
.nav-cross {
  display: none;
  position: absolute;
  top: 0;
  width: 20px;
  height: 2px;
  background-color: #FFFFFF;
  transform: rotate(45deg);
}
.nav-cross::after {
  content: "";
  background-color: #FFFFFF;
  width: 100%;
  height: 2px;
  position: absolute;
  transform: rotate(270deg);
}
.nav-cross.active {
  display: block;
}
.navigation__box {
  width: 100%;
  background-color: #001e42;
  /*   @media (min-width: 992px) {
    left: 51px;
  } */
  /*  @media (max-width: 991px) {
    left: 0;
  } */
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .navigation__box {
    background-color: rgba(0, 0, 0, 0);
    width: max-content;
  }
}
@media (max-width: 1199px) {
  .navigation__box {
    display: none;
    top: 50px;
    border-top: 1px solid #035EC8;
    left: 0;
    position: absolute;
    text-align: center;
  }
  .navigation__box.active {
    display: block;
  }
}
.navigation__box .text {
  font-size: 15px;
  line-height: 24px;
  color: #6B88B9;
}
@media (min-width: 1400px) {
  .navigation__box .text {
    font-size: 16px;
  }
}
.navigation__box a {
  font-size: 15px;
  line-height: 26px;
  color: #FFFFFF;
}
@media (min-width: 1200px) {
  .navigation__box a {
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1400px) {
  .navigation__box a {
    font-size: 20px;
  }
}
.navigation__box a:hover {
  color: #87BEFF;
}
.navigation__btn {
  order: 1;
}
@media (max-width: 1199px) {
  .navigation__btn {
    padding-left: 7px;
    border-left: 1px solid #09388a;
  }
}
@media (max-width: 767px) {
  .navigation__btn {
    display: none;
  }
}
.navigation__cont {
  display: flex;
  flex-grow: 1;
}
@media (max-width: 991px) {
  .navigation__cont {
    justify-content: flex-end;
  }
}
@media (min-width: 1214px) {
  .navigation__cont {
    justify-content: space-between;
    margin-right: 10px;
  }
}
@media (min-width: 1350px) {
  .navigation__cont {
    margin-right: 20px;
  }
}
@media (min-width: 1630px) {
  .navigation__cont {
    margin-right: 75px;
  }
}
.burger {
  display: none;
}
.burger span {
  position: absolute;
  left: 13px;
  top: 19px;
  width: 50%;
  height: 2px;
  background-color: #FFFFFF;
  pointer-events: none;
}
@media (max-width: 991px) {
  .burger {
    display: block;
    position: relative;
    width: 55px;
    height: 40px;
    margin-left: 5px;
    margin-right: 5px;
  }
}
.burger.active span {
  transform: scaleX(0);
}
.burger::before,
.burger::after {
  content: "";
  background-color: #FFFFFF;
  position: absolute;
  width: 50%;
  height: 2px;
  left: 13px;
  pointer-events: none;
}
.burger::before {
  top: 10px;
}
.burger::after {
  bottom: 10px;
}
.burger.active::before {
  top: 50%;
  margin-top: -1px;
  transform: rotate(45deg);
}
.burger.active::after {
  transform: rotate(-45deg);
  bottom: 50%;
  margin-bottom: -1px;
}
.burger.active {
  background-color: #035EC8;
}
.phone-icon .burger-cross,
.mail-icon .burger-cross {
  width: 55px;
  height: 40px;
  position: relative;
  display: none;
  background-color: #035EC8;
}
.burger-cross.active {
  display: block;
}
.burger-cross::before,
.burger-cross::after {
  content: "";
  background-color: #FFFFFF;
  position: absolute;
  width: 50%;
  height: 2px;
  left: 13px;
  pointer-events: none;
}
.burger-cross::before {
  top: 50%;
  margin-top: -1px;
  transform: rotate(45deg);
}
.burger-cross::after {
  transform: rotate(-45deg);
  bottom: 50%;
  margin-bottom: -1px;
}
.benefits {
  padding-bottom: 10px;
  padding-top: 30px;
}
@media (min-width: 850px) {
