    padding-bottom: 40px;
    padding-top: 70px;
  }
}
@media (min-width: 768px) {
  .benefits__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 6px;
  }
}
@media (min-width: 1100px) {
  .benefits__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 40px;
  }
}
.benefits-card .benefits-card__item {
  display: flex;
  align-items: center;
  background-color: #FFFFFF;
}
@media (min-width: 850px) {
  .benefits-card .benefits-card__item {
    flex-direction: column;
    align-items: flex-start;
    padding: 10px;
  }
}
.benefits-card .benefits-card__item:not(:last-child) {
