.benefits-card .benefits-card__text {
  width: 100%;
  font-size: 17px;
  line-height: 24px;
  padding: 20px;
  border-left: 2px solid #F3F3F3;
}
@media (min-width: 850px) {
  .benefits-card .benefits-card__text {
    border-top: 1px solid #F3F3F3;
    border-left: none;
  }
}
@media (min-width: 1100px) {
  .benefits-card .benefits-card__text {
    font-size: 22px;
    line-height: 30px;
  }
}
