.contact-wrap{
  @media (min-width: 768px) {
      width: 50%;
  }
  &--long{
      @media (min-width: 768px) {
          flex-grow: 1;
          width: 100%;
      }
      @media (min-width: 1500px) {
          height: 180px;
      }
  }
}
.contact-link{
  display: block;
  color: #001E42;
  padding-top: 15px;
  @media (min-width: 1200px) {
    margin-top: 15px;
  }
  &::after{
    content: "";
    display: block;
    width: 35px;
    height: 10px;
    background: url(../img/arrow-white.svg) center center no-repeat;
    background-size: auto;
    margin-top: 10px;
    flex: 0 0 auto;
  }
  &--border-blue{
    @media (min-width: 1200px) {
      border-right: 1px solid @color-grey;
    }
  }
  @media (min-width: 781px) {
    padding-left: 30px;
  }
}
.contact-link.open::after{
  display: none;
}
.contact-link::after{
  @media (min-width: 1200px) {
    display: none;
  }
}
.number-courses{
  color: #0C7CFF;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: 500;
  margin-top: 10px;
  @media (min-width: 1200px) {
    color: #09388A;
    font-size: 16px;
    line-height: 22px;
    margin-bottom: 15px;
  }
}
.contact-elem{
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding-top: 13px; */
  padding-bottom: 22px;
  /* &--border{
    border-bottom: 1px solid #c2c9cc;
  } */
  & .contact-elem__img{
      width: 30px;
      height: 30px;
  }
  & .contact-elem__list{
      padding-left: 20px;
      flex-grow: 1;
      & .contact-elem__title{
          color: @color-blue-grey;
          font-size: 15px;
          line-height: 24px;
          /* padding-bottom: 7px; */
          @media (min-width: 1100px) {
              font-size: 16px;
          }
          }

      & .contact-elem__text{
          line-height: 24px;
          /* padding-bottom: 7px; */
          font-weight: 500;
          font-size: 16px;
          @media (min-width: 1100px) {
              font-size: 17px;
          }
          &--big{
              font-size: 16px;
              text-transform: uppercase;
              font-weight: 500;
          }
      }
      & .contact-elem__link{
          color: @color-blue1;
          font-size: 16px;
          line-height: 26px;
          font-weight: 500;
          @media (min-width: 1100px) {
              font-size: 17px;
          }
          &--phone{
              color: @color-black;
              @media (min-width: 1100px) {
                font-size: 22px;
                line-height: 30px;
            }
          }
      }
  }

}
.contact-elem__link:hover{
  text-decoration: underline;
}
.contact-link-wr .contact-elem__list{
  padding-left: 5px;
}
.contact-title{
  padding-bottom: 20px;
}
.contact-wrap .title{
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    padding-bottom: 15px;
    @media (min-width: 768px) {
        min-height: 60px;
    }
    @media (min-width: 950px) {
        height: 90px;
        padding-bottom: 0;
    }
    @media (min-width: 1100px) {
        font-size: 22px;
        line-height: 30px;
        padding-right: 30px;
        margin-bottom: 25px;
    }
}
.contact-wrapper{
    @media (min-width: 1550px) {
        display: flex;
        padding-top: 25px;
    }
}
.number-courses-wr{
  @media (max-width: 1199px) {
    display: none;
}
}
