.partners{
    margin-bottom: 40px;
    @media (min-width: 850px) {
        margin-bottom: 80px;
    }

}
.partners__list{
    & .partners__img-dox{
        background-color: @color-white;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        border-left: 1px solid #c2c9cc;
    }
    @media (min-width: 768px) {
        order: 1;
        flex-grow: 1;
    }
}
.swiper-slide-active .partners__img-dox{
    border-left: 1px solid transparent;
}
.partners__img{
    & img{
        width: 100%;
    }
    @media (min-width: 850px) {
        width: 260px;
        height: 130px;
    }
}
.slider__elem{
    /* padding: 20px 6px; */
    box-sizing: border-box;
}
.part__wrap{
    width: 100%;
    background-color: @color-white;
    padding: 20px 5px;
    @media (min-width: 1200px) {
        padding: 50px;
    }
}
.partners__wrap{
    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }
}
.partners_btn{
    @media (min-width: 768px) {
        order: -1;
    }
}
.partners__title{
    @media (min-width: 768px) {
        order: -1;
        flex-grow: 1;
    }
    &.general-title{
      padding-bottom: 20px;
      @media (min-width: 768px) {
        padding-bottom: 15px;
      }
    }
}
