.courses{
    display: flex;
    @media (min-width: 768px) {
        order: 1;
        flex-grow: 1;
        width: 100%;
    }
}
.courses__head{
    background-color: @color-blue2;
    margin-right: 5px;
    flex: 0 0 auto;
    @media (min-width: 1200px) {
       width: 330px;
    }

}
.courses__list{
    display: flex;
    flex-direction: column;
    padding-top: 15px;
    @media (min-width: 850px) {
        padding-top: 16px;
    }
}
.courses__item{
    padding: 13px;
    height: 47px;
    display: flex;
    align-items: center;
    position: relative;
    height: 50px;
    @media (min-width: 850px) {
      padding-top: 16px;
    }
    &:nth-child(even){
        background-color: @color-blue5;
    }
    &:first-child{
        height: 69px;
        @media (min-width: 781px) {
            padding-top: 20px;
            padding-bottom: 24px;
            height: 82px;
        }
    }
    &:last-child{
      @media (min-width: 781px) {
        height: 57px;
      }
    }
}
.courses__slider{
    padding-bottom: 10px;
    border-bottom: 1px solid @color-grey;
    border-top: 1px solid @color-grey;
    width: 100%;
    padding-bottom: 20px !important;
    @media (min-width: 768px) {
        padding-bottom: 20px !important;
    }
}
.courses__slider-item{
    @media (min-width: 768px) {
        border-right: 1px solid @color-grey;
    }
}
.courses__slider-item:hover {
    background-color: @color-white;
    box-shadow: rgb(255 255 255) 0px 20px 0px 0px;
    /* transform: scale(1.02); */
    transition: transform .25s;
}
.courses__slider-item:hover ul {
    background-color: @color-white;
    box-shadow: 0px -20px 0 0px @color-white;
    transition: transform .25s;
}

.courses__title{
    color: @color-white;
    font-size: 17px;
    line-height: 26px;
    margin-left: 20px;
    display: none;
    @media (min-width: 1200px) {
        display: block;
        width: max-content;
    }
    @media (min-width: 768px) {
        margin-left: 14px;
    }
}
.courses__svg{
    width: 22px;
    height: 22px;
    text-align: center;
}
.courses__svg svg{
    fill: #428ce2;
}

.prof__order{
  display: flex;
  @media (min-width: 1200px) {
    margin: 0 auto;
    box-sizing: border-box;
  }

}
.prof{
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    /* padding-top: 15px; */
    @media (min-width: 768px) {
      margin-bottom: 19px;
    }
    & .prof__item{
        font-size: 16px;
        line-height: 22px;
        font-weight: 500;
        text-align: center;
        height: 50px;
        padding: 15px 3px 0;
        &--blue{
            color: @color-blue2;
        }
        &:nth-child(even){
            background-color: @color-white2;
        }
        &:first-child{
          height: 69px;
          @media (min-width: 781px) {
              height: 82px;
          }
        }

        &:last-child{
          font-size: 17px;
          line-height: 22px;
          @media (min-width: 781px) {
            height: 57px;
            font-size: 22px;
            line-height: 30px;
          }
        }
    }
}
.courses__wrap{
    margin-top: 15px;
    @media (min-width: 768px) {
        display: flex;
    }
}

.courses-popap{
    position: absolute;
    top: -45px;
    left: 10px;
    width: max-content;
    height: 38px;
    background-color: @color-black;
    color: @color-white;
    display: none;
    justify-content: space-between;
    align-items: center;
    padding: 5px;
    z-index: 2;
    & .courses-popap-text{
        position: relative;
        margin: 0 10px;
    }
    & .courses-popap-text::after{
        content: "";
        position: absolute;
        bottom: -28px;
        left: -8px;
        border: 8px solid transparent;
        border-top: 8px solid  @color-black;
    }
}
.courses-popap.open{
    display: flex;
}
