@import "../less/color.less";


/* Roboto*/
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
       url('../fonts/roboto/Roboto-Regular.woff2') format('woff2'),
       url('../fonts/roboto/Roboto-Regular.woff') format('woff');
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
       url('../fonts/roboto/Roboto-Medium.woff2') format('woff2'),
       url('../fonts/roboto/Roboto-Medium.woff') format('woff');
}
/*  */
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
       url('../fonts/oswald/Oswald-Regular.woff2') format('woff2'),
       url('../fonts/oswald/Oswald-Regular.woff') format('woff');
}

:root{
  box-sizing: border-box;
}
*, ::before, ::after{
  box-sizing: inherit;
  -webkit-tap-highlight-color: transparent;

}
html{
  height: 100%;
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 1;
  font-weight: 400;
  color: @color-black;
  text-transform: none;
  background-color: @color-grey-lite;
  height: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}


.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  @media (min-width: 768px) {
    max-width: 714px;
    padding-left: 3px;
    padding-right: 3px;
  }
  @media (min-width: 900px) {
    max-width: 1760px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

