.footer{
    background-color: @color-black;
    text-align: center;
    padding: 15px 0;
    & p{
        font-size: 15px;
        line-height: 22px;
        color: @color-blue-white;
        padding-bottom: 15px;
        @media (min-width: 950px) {
            font-size: 16px;
            line-height: 24px;
            padding-bottom: 10px;
            text-align: left;
        }
    }
    & .politic__link{
        font-size: 15px;
        line-height: 22px;
        color: @color-blue-white;
    }
    & .politic:hover{
        text-decoration: underline;
        cursor: pointer;
    }
    & .made{
        display: flex;
        justify-content: center;
        align-items: flex-end;
        @media (min-width: 950px) {
            width: 30%;
            justify-content: right;
        }
        & .made__text{
            padding-bottom: 0;
            margin-right: 12px;
        }
    }
}
.footer-box{
    @media (min-width: 950px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.footer__cont{
    @media (min-width: 950px) {
        order: -1;
        width: 30%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
    }
}
.footer__img{
    @media (min-width: 950px) {
        width: 30%;
        & img{
            width: 77px;
            height: 70px;
        }
    }
}