.first-screen-wr{
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  display: flex;
  flex-direction: column;
  padding-top: 120px;
  background: url(../img/first-fon.png) bottom no-repeat;
  background-size: contain;
  background-position-y: 440px;
  @media (min-width: 768px) and (max-width: 1199px) {
    padding-top: 250px;
    padding-left: 30px;
    padding-right: 30px;
    background-size: cover;
    background-position-y: -90px;
    flex-direction: row;
    position: relative;
  }
  @media (min-width: 1200px) {
    padding-top: 150px;
    padding-left: 0;
    padding-right: 0;
    flex-direction: row;
    justify-content: center;
    background: none;
  }
  @media (min-width: 1500px) {
    padding-top: 200px;
  }

}
.first-screen{
  background-color:  @color-blue2;
  @media (min-width: 1200px) {
    background: linear-gradient(to right, #f3f3f3 50%, @color-blue2 50%);
  }
  & h1{
    color: @color-white;
    font-size: 22px;
    line-height: 30px;
    font-family: 'Oswald',sans-serif;
    font-weight: 400;
    margin-bottom: 20px;
    @media (min-width: 768px) and (max-width: 1199px) {
    font-size: 28px;
    line-height: 36px;
    position: absolute;
    top: 130px;
    width: 90%;
    }
    @media (min-width: 1200px) {
    font-size: 40px;
    line-height: 52px;
    color: #09388A;
    margin-bottom: 40px;
    }
    @media (min-width: 1500px) {
    margin-bottom: 70px;
    }
  }
}
.contact-wrap-fscr{
  padding-bottom: 15px;
  position: relative;
}

.contact-wrap-fscr-blue{
  @media (max-width: 1199px) {
    border-bottom: 1px solid @color-blue1;
  }
}
.contact-link{
  @media (max-width: 1199px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.contact-wrap-fscr .contact-link.open{
  position: absolute;
  background-color: #f3f3f3;
  width: 100%;
  padding-left: 30px;
  padding-top: 30px;
  z-index: 2;
  flex-direction: column;
  border: 1px solid @color-grey;
  padding-bottom: 30px;
  & .number-courses-wr{
    display: block;
  }
  & .contact-link-wr h3{
    color: #001E42;
    font-size: 22px;
    line-height: 30px;
    margin-bottom: 25px;
  }
  & .number-courses{
    margin-bottom: 25px;
  }
}

.main-text{
  @media (min-width: 1200px) {
    margin-left: auto;
    max-width: 860px;
  }
}
.main-img-wr{
  max-width: 250px;
  margin: 0 auto;
  @media (min-width: 1200px) {
    max-width: 930px;
    margin: 0;
/*     margin-left: 15px; */
  }

}
.main-text{
  width: 100%;
  @media (min-width: 1200px) {
    width: 50%;
  }
}
.main-img{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  @media (min-width: 1200px) {
    width: 50%;
    background: url(../img/first-fon.png) center center no-repeat;
    background-size: cover;
    & img{
      width: 100%;
      height: 640px;
      object-fit: contain;
      object-position: bottom;
    }
  }
  @media (min-width: 1500px) {
    & img{
      height: 715px;
    }
  }
}
.main-img__photo{
  display: block;
  display: flex;
}
.main-courses{
  cursor: pointer;
  display: flex;
  border-top: 1px solid @color-blue1;
  border-bottom: 1px solid @color-blue1;
  margin-bottom: 20px;
  @media (max-width: 1199px) {
    flex-direction: column;
  }
  & .contact-subtitle{
    color: @color-white;
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    @media (min-width: 1200px) {
      color: @color-black;
      font-size: 20px;
      line-height: 28px;
      min-height: 85px;
      margin-bottom: 10px;
    }
  }
  @media (min-width: 1200px) {
    border-top: 1px solid #c2c9cc;
    border-bottom: 1px solid #c2c9cc;
  }
  & .contact-wrap{
    @media (max-width: 1199px) {
      width: 100%;
    }
  }
}
.main-courses__svg svg{
    fill: #09388A;
  }
.main-courses .contact-wrap:hover{
  @media (min-width: 1200px) {
    background-color: @color-white;
    box-shadow: 0px 0px 0px 1px #ffff;
  }
}
.main-courses .contact-wrap:active{
  @media (max-width: 1199px) {
    background-color: #035ec875;
    box-shadow: 0px 0px 15px 5px #035ec875;
  }
}
.main-courses .contact-wrap:hover .contact-link--border-blue {
  @media (min-width: 1200px) {
    border-right: 1px solid transparent;
  }
}
.main-courses .contact-wrap:hover::after {
  @media (min-width: 1200px) {
    opacity: 1;
    right: -79px;
    transition: right .2s;
  }
}
.main-text-wr {
  @media (min-width: 1200px) and (max-width:1760px) {
    padding-left: 20px;
    padding-right: 30px;
  }
  @media (min-width: 1761px) {
    padding-right: 70px;
  }
}
.contact-elem-arrow{
  width: 30px;
  height: 2px;
  background-color: #005dc8;
  position: relative;
  margin-top: 40px;
}
.contact-elem-arrow::after{
  content: "";
  display: block;
  width: 35px;
  height: 10px;
  background: url(../img/arrow-fscr.svg) center center no-repeat;
  background-size: auto;
  flex: 0 0 auto;
  position: absolute;
  top: -4px;
  left: 0;
}
.main-courses .contact-wrap:hover .contact-elem-arrow::after {
  @media (min-width: 1200px){
    left: 15px;
    transition: left .2s;
  }
}
.contact-wrap-fscr{
  position: relative;
  overflow: hidden;
}
.contact-wrap-fscr::after{
  @media (min-width: 1200px){
  content: "";
  display: block;
  width: 307px;
  height: 180px;
  background: url(../img/figure2.svg) center center no-repeat;
  background-size: contain;
  position: absolute;
  transform: rotate(270deg) scaleX(-1);
  top: 39%;
  right: -90px;
  opacity: 0;
  } 
}