.benefits{
    padding-bottom: 10px;
    padding-top: 30px;
    @media (min-width: 850px) {
        padding-bottom: 40px;
        padding-top: 70px;
    }
}
.benefits__list{
    @media (min-width: 768px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 6px;
    }
    @media (min-width: 1100px) {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-gap: 40px;
    }
}
.benefits-card{
    & .benefits-card__item{
        display: flex;
        align-items: center;
        background-color: @color-white;
        @media (min-width: 850px) {
            flex-direction: column;
            align-items: flex-start;
            padding: 10px;
        }
    }
    & .benefits-card__item:not(:last-child){
        margin-bottom: 6px;
        @media (min-width: 768px) {
            margin-bottom: 0;
        }
    }
    & .benefits-card__img{
      flex: 0 0 auto;
        width: 50px;
        height: 50px;
        margin: 20px;
        position: relative;
        @media (min-width: 850px) {
            width: 63px;
            height: 70px;
        }
        & img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
        &::after{
            content: "";
            display: block;
            width: 2px;
            height: 100%;
            background-color: @color-blue1;
            position: absolute;
            bottom: 0;
            right: -22px;
            @media (min-width: 850px) {
                bottom: -22px;
                right: 0;
                height: 2px;
                width: 100%;
            }
        }
    }
    & .benefits-card__text{
        width: 100%;
        font-size: 17px;
        line-height: 24px;
        padding: 20px;
        border-left: 2px solid @color-grey-lite;
        @media (min-width: 850px) {
            border-top: 1px solid @color-grey-lite;
            border-left: none;
        }
        @media (min-width: 1100px) {
            font-size: 22px;
            line-height: 30px;
        }
    }
}
