.header{
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
  @media (max-width: 1199px) {
    background-color: @color-black;
  }
  
}
.header.scrolled-deck{
  @media (min-width: 1200px) {
    background-color: @color-white;
    & .navigation__list .navigation__item a{
      color: @color-black;
    }
    & .navigation__box a{
      color: @color-blue2;
    }
    & .mail-icon::after{
      opacity: 1;
    }
    & .phone-icon::after{
      opacity: 1;
    }
  }
}
.header.scrolled-deck .navigation__box a:hover{
  color: @color-blue1;
}

.main-nav{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  /* position: relative; */
  @media (max-width: 991px) {
    margin-bottom: 0;
/*     margin: 10px 0; */
    min-height: 50px;
  }
  @media (min-width: 992px) {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
.main-nav-left, .main-nav-right{
  display: flex;
  align-items: center;
  justify-content: space-between;
  @media (min-width: 1200px) {
    width: 50%;
  }
  @media (min-width: 1520px) {
    width: 45%;
  }
  
}
.main-nav-left{
  @media (max-width: 1199px) {
    flex: 1 0 auto;
  }
  @media (min-width: 1200px) {
    padding-right: 25px;
  }
}
.main-nav-right{
  flex-wrap: wrap;
  justify-content: space-between;
  @media (max-width: 991px) {
    width: 75%;
  }
  @media (min-width: 1200px) and (max-width: 1519px) {
    padding-left: 25px;
  }
}
.nav-logo{
  background: @color-white;
  display: block;
  width: 90px;
  height: 90px;
  flex: 0 0 auto;
  @media (max-width: 1199px) {
    position: absolute;
    top: 0;
  }
  & img{
    width: 100%;
    height: auto;
  }
  @media (min-width: 1200px) {
    background-color: transparent;
    width: 90px;
    height: 90px;
  }
  @media (min-width: 1670px) {
    width: 120px;
    height: 110px;
  }
}
/* .main-nav.active .nav-logo.open{
  @media (max-width: 991px) {
    width: 40px;
    height: 40px;
    top: 5px;
    background-color: transparent;
  }
} */
.nav-logo.open{
  @media (max-width: 1199px) {
    width: 40px;
    height: 40px;
    top: 5px;
    background-color: transparent;
  }
}
.nav-logo.scrolled{
  width: 60px;
  height: 60px;
  @media (max-width: 1199px) {
    width: 40px;
    height: 40px;
    top: 5px;
    background-color: transparent;
  }
}
.navigation{
  flex-grow: 1;
  @media (max-width: 991px) {
    display: none;
    position: absolute;
    width: 100%;
    left: 0;
    top: 50px;
    border-top: 1px solid @color-blue1;
    background-color: @color-black;
    padding-bottom: 30px;
    &.open{
      display: block;
    }
  }
}
.navigation__list{
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  @media (max-width: 991px) {
    flex-direction: column;
    align-items: center;
  }
/*   @media (min-width: 1520px) {
    justify-content: flex-end;
  } */
  & .navigation__item{
    position: relative;
    @media (max-width: 991px) {
      margin-top: 25px;
      margin-left: 0;
    }
    margin-left: 20px;

    @media (min-width: 1200px) {
      margin-left: 15px;
    }
    @media (min-width: 1350px) {
      margin-left: 30px;
    }
    @media (min-width: 1670px) {
      margin-left: 45px;
    }
    & a{
      font-size: 17px;
      line-height: 24px;font-weight: 500;
      color: @color-black;
      @media (max-width: 1199px) {
        font-weight: 500;
        color: @color-white;
      }
    }
    &:hover a{
      color: #035EC8;
    }
  }
}
.navigation__item a::after{
  content: " ";
  width: 100%;
  height: 2px;
  background-color: #035EC8;
  position: absolute;
  left: 0;
  bottom: -3px;
  transform:scaleX(0);
}
.navigation__item:hover a::after{
  transform:scaleX(1);
  transition: transform 0.25s;
}
.navigation__item--btn{
  @media (min-width: 768px) {
    display: none;
  }
}

.mail-icon::before{
  content: "";
  display: block;
  width: 30px;
  height: 30px;
 /*  margin-right: 20px; */
  background: url(../img/nav-e-mail.svg) center center no-repeat;
  background-size: auto;
  @media (min-width: 1200px) {
    margin-right: 15px;
  }
}
.mail-icon::after{
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
  width: 30px;
  height: 30px;
/*   margin-right: 10px; */
  background: url(../img/nav-e-mail-blue.svg) center center no-repeat;
  background-size: auto;
  opacity: 0;
  @media (max-width: 1200px) {
    display: none;
  }
}
.mail-icon.active::before,
.mail-icon.active::after{
  display: none;
}
.phone-icon::before{
  content: "";
  display: block;
  width: 30px;
  height: 30px;
/*   margin-right: 20px; */
  background: url(../img/nav-phone.svg) center center no-repeat;
  background-size: auto;
  @media (min-width: 1200px) {
    margin-right: 15px;
  }
}
.phone-icon::after{
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: 0;
  width: 30px;
  height: 30px;
/*   margin-right: 20px; */
  background: url(../img/nav-phone-blue.svg) center center no-repeat;
  background-size: auto;
  opacity: 0;
  @media (max-width: 1200px) {
    display: none;
  }
}
.phone-icon.active::before,
.phone-icon.active::after{
  display: none;
}

.navigation__cont-wrap{
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 0 0 auto;
  @media (min-width: 1200px) {
    position: relative;
  }
  @media (max-width: 1199px) {
    height: 40px;
    width: 60px;
    padding-right: 5px;
    margin-left: 5px;

  }
  @media (min-width: 1200px) {
    margin-right: 5px;
    padding: 0;
    justify-content: space-between;
  }
  @media (max-width: 1199px) {
    border-right: 1px solid #09388A;
    margin-right: 0;
  }
}

.nav-cross{
  display: none;
  position: absolute;
  top: 0;
  width: 20px;
  height: 2px;
  background-color: @color-white;
  transform: rotate(45deg);
  &::after{
    content: "";
    background-color:  #FFFFFF;
    width: 100%;
    height: 2px;
    position: absolute;
    transform: rotate(270deg);
  }
}
.nav-cross.active{
  display: block;
}

.navigation__box{
  width: 100%;
  background-color: #001e42;
  @media (min-width: 1200px) {
    background-color: rgba(0, 0, 0, 0);
    width: max-content;
  }
/*   @media (min-width: 992px) {
    left: 51px;
  } */
 /*  @media (max-width: 991px) {
    left: 0;
  } */
  @media (max-width: 1199px) {
    display: none;
    top: 50px;
    border-top: 1px solid @color-blue1;
    left: 0;
    position: absolute;
    text-align: center;
    &.active{
      display: block;
    }
  }
  display: flex;
  flex-direction: column;
  & .text{
    font-size: 15px;
    line-height: 24px;
    color: #6B88B9;
    @media (min-width: 1400px) {
      font-size: 16px;
    }
  }
  & a{
    font-size: 15px;
    line-height: 26px;
    color: #FFFFFF;
    @media (min-width: 1200px) {
      font-size: 18px;
      line-height: 24px;
    }
    @media (min-width: 1400px) {
      font-size: 20px;
    }
    &:hover{
      color: #87BEFF;
    }
  }
}
.navigation__btn{
  order: 1;
  @media (max-width: 1199px) {
    padding-left: 7px;
    border-left: 1px solid #09388a;
  }
  @media (max-width: 767px) {
    display: none;
  }
}
.navigation__cont{
  display: flex;
  flex-grow: 1;
  @media (max-width: 991px) {
    justify-content: flex-end;
  }
  @media (min-width: 1214px) {
    justify-content: space-between;
    margin-right: 10px;
  }
  @media (min-width: 1350px) {
    margin-right: 20px;
  }
  @media (min-width: 1630px) {
    margin-right: 75px;
  }
}

/////////////////// burger
.burger{
  display: none;
  & span{
    position: absolute;
    left: 13px;
    top: 19px;
    width: 50%;
    height: 2px;
    background-color: #FFFFFF;
    pointer-events: none;
  }
  @media (max-width: 991px) {
    display: block;
    position: relative;
    width: 55px;
    height: 40px;
    margin-left: 5px;
    margin-right: 5px;
  }
  &.active span {
    transform: scaleX(0);
  }
}
.burger::before,
.burger::after {
  content: "";
  background-color:  #FFFFFF;
  position: absolute;
  width: 50%;
  height: 2px;
  left: 13px;
  pointer-events: none;
}
.burger::before{
  top: 10px;
}
.burger::after{
  bottom: 10px;
}

.burger.active::before {
  top: 50%;
  margin-top: -1px;
  transform: rotate(45deg);
}
.burger.active::after {
  transform: rotate(-45deg);
  bottom: 50%;
  margin-bottom: -1px;
}
.burger.active{
  background-color: @color-blue1;
}
//////////////burger-cross
.phone-icon .burger-cross,
.mail-icon .burger-cross{
  width: 55px;
  height: 40px;
  position: relative;
  display: none;
  background-color: @color-blue1;
}
.burger-cross.active{
  display: block;
}
.burger-cross::before,
.burger-cross::after {
    content: "";
    background-color:  #FFFFFF;
    position: absolute;
    width: 50%;
    height: 2px;
    left: 13px;
    pointer-events: none;
}
.burger-cross::before{
  top: 50%;
  margin-top: -1px;
  transform: rotate(45deg);
}
.burger-cross::after{
  transform: rotate(-45deg);
  bottom: 50%;
  margin-bottom: -1px;
}