.general-title{
    font-family: 'Oswald', sans-serif;
    font-size: 30px;
    line-height: 36px;
    padding-bottom: 15px;
    color: @color-black;
    letter-spacing: 0.3px;
    font-weight: 400;
    &--white{
        color: @color-white;
    }
    @media (min-width: 768px) {
        padding-bottom: 20px;
        font-size: 34px;
        line-height: 42px;
    }
    @media (min-width: 1200px) {
        font-size: 50px;
        line-height: 60px;
    }
}
.genetal-btn{
    margin: 15px auto;
    width: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (min-width: 1150px) {
      margin: 0;
      margin-bottom: 25px;
      align-self: flex-end;
    }
    & span{
        margin: 0 auto;
        width: 36px;
        height: 100%;
    }
    & .genetal-btn__element{
        background-color: @color-white;
        border-radius: 50%;
        border: none;
        width: 35px;
        height: 35px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        padding: 0;
        cursor: pointer;
    }
}
.genetal-btn__element:hover{
    background-color: @color-blue1;
}
.genetal-btn__element.swiper-button-disabled{
  cursor: auto;
}
.genetal-btn__element.swiper-button-disabled svg {
    fill: @color-grey;
}
.genetal-btn__element.swiper-button-disabled:hover svg {
    fill: @color-grey;
}

.genetal-btn__element svg {
    fill: #035ec8;
}

.genetal-btn__element:hover svg {
    fill: @color-white;
}

.genetal-btn__element:disabled{
    background-color: @color-white;
}

.general-order{
    background-color: @color-blue1;
    width: 210px;
    margin: 0 auto;
    padding: 12px 5px;
    border: none;
    color: @color-white;
    font-size: 17px;
    line-height: 26px;
    cursor: pointer;
    @media (min-width: 1500px) {
      width: 304px;
    }
    &:hover{
        background-color: #0C7CFF;
    }
    &--modal{
        width: 100%;
        margin: 0;
    }
    &--nav{
        width: 164px;
        padding: 7px 5px;
        @media (min-width: 1470px) {
            width: 216px;
        }
        @media (min-width: 1200px) {
            padding: 12px 5px;
        }
    }
}
.prof__order .general-order{
    background-color: @color-blue1;
    width: 220px;
    margin: 0 auto;
    padding: 12px 5px;
    border: none;
    color: @color-white;
    font-size: 17px;
    line-height: 26px;
    text-align: center;
    cursor: pointer;
    @media (min-width: 768px) {
      width: 306px;
    }
    @media (min-width: 1200px) {
      width: 286px;
    }
    @media (min-width: 1500px) {
      width: 304px;
    }
    &:hover{
        background-color: #0C7CFF;
    }
