.general-btn-cross{
   width: 26px;
   height: 26px;
   background-color: @color-blue2;
   display: flex;
   align-items: center;
   justify-content: center;
   cursor: pointer;
   & span{
       display: block;
       height: 2px;
       width: 23px;
       background-color: @color-white;
       transform: rotate(225deg);
       &::before{
          content: "";
          display: block;
          height: 2px;
          width: 100%;
          background-color: @color-white;
          transform: rotate(90deg);
       }
   }
   &.press{
    background-color: @color-blue1;
   }
   &--modal{
       position: absolute;
       top: 0;
       right: 0;
       width: 30px;
       height: 30px;
       z-index: 9;
       @media (min-width: 850px) {
        width: 50px;
        height: 50px;
    }
       & span{
           width: 30px;
       }
   }
   &:hover{
        background-color: #035EC8;
   }
}
