.gallery{
    padding: 35px 0;
    padding-bottom: 85px;
    background-color: @color-blue2;
    position: relative;
    @media (min-width: 768px) {
      padding: 30px 0;
    }
    @media (min-width: 1200px) {
      padding: 55px 0;
    }
  & .gallery-title{
    padding-bottom: 15px;
  }

    & .container{
        @media (min-width: 768px) {
            position: relative;
        }
    }
}
.gallery-card{
  position: relative;
  box-sizing: content-box;
}

.gallery-card img{
    display: block;
    object-fit: cover;
    /* @media (min-width: 1150px) {
      width: 546px;
      height: 360px;
  } */
}

.gallery .genetal-btn{
    position: absolute;
    bottom: 30px;
    right: calc(50% - 50px);
    margin: 0;
    height: 37px;
    @media (min-width: 768px) {
        top: 5px;
        right: calc(7% - 50px);
    }
}
.gallery-img{
  display: block;
  width: 100%;
  height: 145px;
  position: relative;
  overflow: hidden;
  @media (min-width: 768px) {
    height: 230px;
  }
  @media (min-width: 1200px) {
    height: 360px;
  }
}
.gallery-img img{
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform .2s;
}


.gallery-img:hover img {
  transform: scale(1.1);
}

.gallery-card::before{
  content: " ";
  display: block;
  width: 60px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  margin-top: -30px;
  margin-left: -30px;
  background: url(../img/full.svg) center center no-repeat;
    background-size: auto;
  pointer-events: none;
  opacity: 0;
  transform: rotate(-90deg);
  transition: opacity .2s, transform .2s;
}

.gallery-card:hover::before {
  opacity: 1;
  transform: rotate(0);
}
.gallery-slider .swiper-slide:nth-child(odd) {
  width: 62%;
}
.gallery-slider .swiper-slide:nth-child(even) {
  width: 32%;
}
@media (min-width: 768px) {
  .gallery-slider .swiper-slide:nth-child(odd) {
    width: 45%;
  }
  .gallery-slider .swiper-slide:nth-child(even) {
    width: 28%;
  }
}
@media (min-width: 1200px) {
  .gallery-slider .swiper-slide:nth-child(odd) {
    width: 30%;
  }
  .gallery-slider .swiper-slide:nth-child(even) {
    width: 15%;
  }
}
@media (min-width: 1500px) {
  .gallery-slider .swiper-slide:nth-child(odd) {
    width: 27%;
  }
  .gallery-slider .swiper-slide:nth-child(even) {
    width: 17%;
  }
}