.description-section{
    margin-bottom: 40px;
    @media (min-width: 850px) {
        margin-bottom: 77px;
    }
}
.description-wrap{
    @media (min-width: 768px) {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 6px;
    }
    @media (min-width: 1100px) {
        grid-gap: 40px;
    }
}
.description{
    background-color: @color-blue2;
    padding: 25px 20px 0;
    margin-bottom: 12px;

    @media (min-width: 768px) {
        padding: 20px 20px 0;
        margin-bottom: 0;
        grid-column-start: 1;
        grid-column-end: 2;
    }
    @media (min-width: 1200px) {
        padding: 50px 50px 0;
    }
    & .description-box{
        background: url(../img/description-backg.svg) bottom right no-repeat;
        background-size: auto;
        padding-bottom: 30px;
        @media (min-width: 1200px) {
            min-height: 421px;
            background-size: 465px 220px;
        }
    }

    & .description__all{
        color: #0C7CFF;
        font-size: 16px;
        line-height: 26px;
        font-weight: 500;
        margin-top: 15px;
        display: block;
        @media (min-width: 1200px) {
            display: none;
        }
    }
    & .description__hid{
        display: none;
        color: #0C7CFF;
        font-size: 16px;
        line-height: 26px;
        font-weight: 500;
        margin-top: 15px;
        @media (min-width: 1200px) {
            display: none;
        }
    }
}
.description .description__hid.open{
    display: block;
}
.description .description__all.hidden{
    display: none;
}
.description__all:hover{
    text-decoration: underline;
}
.description__text{
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 5;
    overflow: hidden;
    @media (min-width: 1200px) {
        -webkit-line-clamp: 100;
    }
    & p{
        color: @color-white;
        font-size: 15px;
        line-height: 22px;
        display: inline;
        &:after{
            content: ' ';
            display: block;
            width: 15px;
            height: 15px;
        }
        @media (min-width: 1100px) {
            font-size: 17px;
            line-height: 27px;
            padding-bottom: 15px;
        }
    }
}
.description__text.open{
    -webkit-line-clamp: 1000;
}


.preview__video {
    position: relative;
    overflow: hidden;
    @media (min-width: 768px) {
        grid-column-start: 2;
        grid-column-end: 3;
    }
}

.preview__video-text {
    position: relative;
}

.preview__video-text::after {
    content: " ";
    position: absolute;
    z-index: -1;
    left: 0;
    top: 0;
    border-radius: 0 10px 0 0;
    display: block;
    width: 100%;
    height: 100%;
    background: #09388A;
    transform: skewY(14deg) translateY(25%);
}

