.choice-box{
    position: relative;
    overflow: auto;
    margin: 0 -12px;
    display: flex;
    margin-bottom: 15px;
    @media (min-width: 768px) {
        order: -1;
        flex-grow: 1;
    }
}

.personal__choice{
    flex: 1 0 auto;
    display: flex;
    align-items: center;
    padding: 0 12px;
}
.personal__box{
    // padding-left: 10px;
}
.personal{
    padding-bottom: 20px;
    @media (min-width: 768px) {
      padding-bottom: 42px;
  }
  & .container{
      @media (min-width: 1200px) {
        padding-right: 20px;
    }
  }
}

.personal-nav{
        flex: 0 0 auto;
        background-color: @color-white;
        color: @color-blue2;
        font-size: 14px;
        line-height: 20px;
        text-transform: uppercase;
        font-weight: 500;
        border-radius: 25px;
        width: max-content;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px 15px;
        cursor: pointer;
        @media (min-width: 850px) {
          font-size: 16px;
          line-height: 24px;
        }

        &:not(:first-child) {
            margin-left: 13px;
        }
        & >span{
            display: block;
            pointer-events: none;
        }
        &:hover > span{
            color: @color-blue1;
        }
        &.active{
          cursor: context-menu;
        }
        &.active:hover > span{
            color: @color-white;
        }

        &--boss::before{
            content: "";
            display: block;
            width: 12px;
            height: 12px;
            background-color: @color-red;
            border-radius: 50%;
            margin-right: 5px;
        }
        &--staff::before{
            content: "";
            display: block;
            width: 12px;
            height: 12px;
            background-color: @color-green;
            border-radius: 50%;
            margin-right: 5px;
        }
        &--some::before{
            content: "";
            display: block;
            width: 12px;
            height: 12px;
            background-color: @color-blue6;
            border-radius: 50%;
            margin-right: 5px;
        }
        /* active у кнопок */
        &--all.active{
            background-color: @color-blue2;
            color: @color-white;
        }
        &--boss.active{
            background-color: @color-red;
            color: @color-white;
            &::before{
                background-color: @color-white;
            }
        }
        &--staff.active{
            background-color: @color-green;
            color: @color-white;
            &::before{
                background-color: @color-white;
            }
        }
        &--some.active{
            background-color: @color-blue6;
            color: @color-white;
            &::before{
                background-color: @color-white;
            }
        }



        // active


                /* active у кнопок */


                &--all {
                    background: linear-gradient(to right, @color-blue2 50%, #fff 50%) 100% top / 200% no-repeat;
                    background-color: @color-blue2;
                }

                &--all.active{
                    background-position: 0 0;
                    transition: background-position .2s;
                }



                &--boss {

                    background: linear-gradient(to right, @color-red 50%, #fff 50%) 100% top / 200% no-repeat;
                    background-color: @color-red;

                }

                &--boss.active {
                    background-position: 0 0;
                    transition: background-position .2s;
                    &::before{
                        background-color: @color-white;
                    }
                }


                &--staff {

                    background: linear-gradient(to right,  @color-green 50%, #fff 50%) 100% top / 200% no-repeat;
                    background-color:  @color-green;

                }

                &--staff.active {
                    background-position: 0 0;
                    transition: background-position .2s;
                    &::before{
                        background-color: @color-white;
                    }
                }



                &--some {

                    background: linear-gradient(to right,  @color-blue6 50%, #fff 50%) 100% top / 200% no-repeat;
                    background-color:  @color-blue6;

                }

                &--some.active {
                    background-position: 0 0;
                    transition: background-position .2s;
                    &::before{
                        background-color: @color-white;
                    }
                }

}

.personal__swiper {
    flex: 0 0 auto;
    width: 100%;
}

.personal-card{
    background-color: @color-white;
    width: 220px;
    padding: 10px;
    border-top-right-radius: 25px;
    box-sizing: border-box;
    @media (min-width: 1200px) {
        border-top-right-radius: 0;
        border-top-left-radius: 25px;
        padding: 15px;
     }
    & .personal-card__img{
        margin-bottom: 10px;
        @media (min-width: 1150px) {
            margin-bottom: 0;
        }
    }
    & .personal-card__photo img{
        @media (min-width: 768px) {
            width: 187px;
            height: 260px;
            object-fit: cover;
        }
        @media (min-width: 1200px) {
            width: 250px;
            height: 350px;
            object-fit: cover;
        }
    }
    & .personal-card__name{
        margin-bottom: 10px;
        font-size: 17px;
        line-height: 24px;
        padding-left: 10px;
        @media (min-width: 768px) {
            min-height: 50px;
        }
        @media (min-width: 1150px) {
          font-size: 22px;
          line-height: 30px;
          margin-bottom: 17px;
          padding-left: 15px;
        }
    }
    & .personal-card__text{
        margin-bottom: 10px;
        font-size: 14px;
        line-height: 20px;
        font-weight: 500;
        color: @color-blue2;
        text-transform: uppercase;
        padding-left: 10px;
        @media (min-width: 1150px) {
          font-size: 16px;
          line-height: 24px;
          padding-left: 15px;
          padding-right: 34px;
        }
    }
}
.personal__card-box{
    position: relative;
    min-height: 422px;
    @media (min-width: 850px) {
      min-height: 380px;
  }
    @media (min-width: 1150px) {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: flex-start;
    }
    &::after{
        content: "";
        display: block;
        top: -3px;
        right: -7px;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        margin-right: 5px;
        border: 10px solid @color-grey-lite;
        position: absolute;
        @media (min-width: 1150px) {
            left: 0;
            width: 40px;
            height: 40px;
            border: 14px solid @color-grey-lite;
        }
    }
    &--boss::after{
      background-color: @color-red;
    }
    &--some::after{
      background-color: @color-blue3;
    }
    &--staff::after{
      background-color: @color-green;
    }

    & .personal-card-descr{
        @media (min-width: 1150px) {
            width: 50%;
            padding-top: 60px;
        }
    }
}
.personal__desctop{
    @media (min-width: 768px) {
        display: flex;
        flex-wrap: wrap;
    }
}
.personal__title{
    @media (min-width: 768px) {
        order: -1;
        margin-right: 60px;
        padding-bottom: 20px;
    }
}
.personal__btn{
    @media (min-width: 768px) {
        order: -1;
    }
}
.genetal-btn.personal__btn{
    @media (min-width: 768px) {
        margin: 0 auto;
        margin-bottom: 15px;
    }
}

